<template>
  <div class="tags">
    <a class="tag" @click="toggleCategory('all')" :class="{'is-active' : choosed.includes('all')}">all</a>
    <a class="tag"
      v-for="{ name } in categories"
      v-bind:key="`c${name}`"
      :class="{'is-active' : choosed.includes(name)}"
      @click="toggleCategory(name)"
    >
      {{ name }}
    </a>
  </div>
</template>

<script>
export default {
  name: 'category-picker',
  props: {
    categories: Array,
    choosedCategories: Array,
  },
  data: () => ({
    choosed: [],
  }),
  created() {
    this.choosed = this.choosedCategories;
  },
  watch: {
    choosed() {
      if (this.choosed.length === 0) {
        this.choosed.push('all');
      }
      this.$emit('update:choosedCategories', this.choosed);
    },
  },
  methods: {
    toggleCategory(name) {
      if (this.choosed.includes(name)) {
        this.choosed = this.choosed.filter(category => category !== name);
      } else {
        this.choosed = this.choosed.filter(category => category !== 'all');
        this.choosed.push(name);
      }
    },
  },
};
</script>

<style scoped>
.is-active {
  background-color: #F1E685;
  color: #3A4541;
}
</style>



