<template>
  <div class="column">
    <div v-if="displayType === 'modal'">
      <word-modal
        :emoji="emoji"
        :translation="translation"
        :category="category"
        :isActive.sync="isActive"
      ></word-modal>
      <a @click="isActive = true">
        <span>{{ emoji }}</span>
      </a>
    </div>
    <div v-else-if="displayType === 'checkbox'">
      <span class="tooltip" :data-tooltip="translation">
      <a>{{ emoji }}</a>
      </span>
    </div>
  </div>
</template>

<script>
import WordModal from '@/components/WordModal';

export default {
  name: 'word-item',
  props: {
    name: String,
    emoji: String,
    translation: String,
    category: String,
    displayType: String,
  },
  components: {
    WordModal,
  },
  data: () => ({
    isActive: false,
  }),
};
</script>

<style scoped>
span {
  font-size: 2.5rem;
}
.modal {
  animation-duration: 0.15s;
  -webkit-animation-duration: 0.15s;
  animation-delay: 0s;
  -webkit-animation-delay: 0s;
}
.tooltip:focus::after, .tooltip:hover::after {
  background-color: #FFF488;
  color: #3A4541;
}
</style>



