<template>
  <div class="modal animated fadeIn" :class="{ 'is-active': isActive }">
    <div @click="closeModal" class="modal-background"></div>
    <div class="modal-content">
      <div class="card">
        <div class="card-image">
          <span>{{ emoji }}</span>
        </div>
        <div class="card-content">
          <div class="media">
            <p class="title is-1">{{ translation }}</p>
          </div>
          <a>#{{ category }}</a>
        </div>
        <div class="card-footer">
          <div class="buttons is-centered">
            <button v-clipboard:copy="translation" class="button is-info">Copy translation</button>
            <button v-clipboard:copy="emoji" class="button is-success">Copy emoji</button>
            <button @click="closeModal" class="button is-danger">Close</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'word-modal',
  props: {
    isActive: {
      type: Boolean,
      default: () => false,
    },
    translation: String,
    emoji: String,
    category: String,
  },
  methods: {
    closeModal() {
      this.$emit('update:isActive', false);
    },
  },
};
</script>

<style scoped>
span {
  font-size: 3.5rem;
}

.card {
  text-align: center !important;
  margin-left: auto;
  margin-right: auto;
  overflow-y: hidden;
  overflow-x: hidden;
}

.title {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.buttons {
  padding: 1rem;
  width: 100%;
}

.button {
  border: 0;
  height: 2.25rem;
  line-height: 2.25rem;
  padding: 0 0.625rem;
  width: 9rem;
}

div {
  font-size: 1.5rem;
}

.card-content {
  padding: 0;
}

.modal-content {
  border: 3px solid black;
  background-color: #C2D7CF;
  box-shadow: 0 15px 35px rgba(50,50,93,.1), 0 5px 15px rgba(0,0,0,.07);
}

.modal-background {
  background-color: inherit;
}
</style>



