<template>
  <div class="card">
    <div class="card-content">
      <div class="columns is-multiline is-mobile">
        <word-item
          v-for="{ name: wordName, emoji, translations } in words"
          v-if="getTranslation(translations) !== 'no translation' || !translatedOnly"
          :key="wordName"
          :name="wordName"
          :emoji="emoji"
          :category="name"
          :translation="getTranslation(translations)"
          :displayType="displayType"
        ></word-item>
      </div>
    </div>
  </div>
</template>

<script>
import WordItem from '@/components/WordItem';

export default {
  name: 'word-category',
  props: {
    name: String,
    words: Array,
    language: String,
    displayType: String,
    translatedOnly: {
      type: Boolean,
      default: () => false,
    },
  },
  components: {
    WordItem,
  },
  methods: {
    getTranslation(translations) {
      return Object.prototype.hasOwnProperty.call(translations, this.language) ? translations[this.language] : 'no translation';
    },
  },
};
</script>

<style scoped>
.card {
  margin-top: 2rem;
  border-top: 3px solid #7957d5;
  ;
}
</style>



