<template>
  <div v-if="translatedWordCount === 0">
    <no-translations></no-translations>
  </div>
  <div v-else>
    <word-info
      :wordCount="wordCount"
      :translatedWordCount="translatedWordCount"
    ></word-info>

    <div class="card">
      <category-picker
        :categories="categories"
        :choosedCategories.sync="choosedCategories"
      ></category-picker>
      <a class="button" v-if="translatedWordCount > 0 && wordCount < translatedWordCount" @click="translatedOnly = !translatedOnly">
        Show {{ translatedOnly ? 'all' : 'translated' }}
      </a>
      <a class="button" @click="displayType = displayType === 'modal' ? 'checkbox' : 'modal'">
        Display as {{ displayType }}
      </a>
    </div>

    <word-category
      v-for="{ name, words } in categories"
      v-if="choosedCategories.includes('all') || choosedCategories.includes(name)"
      v-bind:key="name"
      :name="name"
      :words="words"
      :language="language"
      :translatedOnly="translatedOnly"
      :displayType="displayType"
    ></word-category>

    <button class="button to-top" v-scroll-to="'#app'"> Scroll to top</button>
  </div>
</template>

<script>
import {
  getCategories,
  getWordCount,
  getTranslatedWordCount,
} from '@/api';
import WordInfo from '@/components/WordInfo';
import CategoryPicker from '@/components/CategoryPicker';
import WordCategory from '@/components/WordCategory';
import NoTranslations from '@/components/NoTranslations';

export default {
  name: 'language',
  components: {
    WordInfo,
    WordCategory,
    CategoryPicker,
    NoTranslations,
  },
  data: () => ({
    translatedOnly: false,
    categories: [],
    wordCount: 0,
    translatedWordCount: 0,
    choosedCategories: ['all'],
    displayType: 'modal',
  }),
  created() {
    this.categories = getCategories();
    this.wordCount = getWordCount();
    this.translatedWordCount = getTranslatedWordCount(this.language);
  },
  watch: {
    language() {
      this.translatedWordCount = getTranslatedWordCount(this.language);
      this.translatedOnly = false;
    },
  },
  computed: {
    language() {
      return this.$route.params.name;
    },
  },
};
</script>

<style scoped>
.card {
  padding: 0.5rem;
}

.to-top {
  margin-top: 1rem;
  width: 100%;
}

.button {
  height: 2.25rem;
  line-height: 2.25rem;
  padding: 0 0.625rem;
}
</style>



